<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input
            v-model="filters.name"
            placeholder="模糊查询"
            @keydown.enter.native="getOrders"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getOrders">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addDialog = true">新增</el-button>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="matchInsurance">按照规则匹配所有未匹配渠道的保单</el-button>
        </el-form-item>
        
      </el-form>
    </el-col>

    <!--列表-->
    <el-table :data="orders" highlight-current-row style="width: 100%">


      <el-table-column prop="channel_name" label="匹配渠道" width="200" sortable>
      </el-table-column>
         <el-table-column prop="applicant" label="投保人" width="200" sortable>
      </el-table-column>
      <el-table-column prop="insured" label="被保险人" width="200" sortable>
      </el-table-column>

      <el-table-column prop="vehicel_owner" label="车主" sortable>
      </el-table-column>

    

      <el-table-column label="操作" width="320">
        <template scope="scope">
          <!-- <el-button size="small" @click="handleEdit(scope.$index, scope.row)">查看详情</el-button>-->
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button
          >

          <el-button
            type="danger"
            size="small"
            @click="delOrder(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="pageSize"
        :total="total"
        style="float: right"
      >
      </el-pagination>
    </el-col>

    <!--新增dialog  -->
    <el-dialog title="新增用户" :visible.sync="addDialog">
      <el-form class="demo-form-inline" :model="addUserinfo" label-width="80px">
        <el-form-item label="渠道">
          <el-input
            v-model="addUserinfo.channel_name"
            auto-complete="off"
          ></el-input>
        </el-form-item>

        <el-form-item label="投保人">
          <el-input
            v-model="addUserinfo.applicant"
            auto-complete="off"
          ></el-input>
        </el-form-item>

         <el-form-item label="被保险人">
          <el-input
            v-model="addUserinfo.insured"
            auto-complete="off"
          ></el-input>
        </el-form-item>

    <el-form-item label="车主">
          <el-input
            v-model="addUserinfo.vehicel_owner"
            auto-complete="off"
          ></el-input>
        </el-form-item>

        
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false">取 消</el-button>
        <el-button type="primary" @click="addUser()">提交</el-button>
      </div>
    </el-dialog>

    <!-- 新增dialog 结束 -->

    <!--编辑dialog  -->
    <el-dialog title="编辑供应商" :visible.sync="editDialog">
      <el-form
        class="demo-form-inline"
        :model="editUserinfo"
        label-width="80px"
      >
       <el-form-item label="渠道">
          <el-input
            v-model="editUserinfo.channel_name"
            auto-complete="off"
          ></el-input>
        </el-form-item>

        <el-form-item label="投保人">
          <el-input
            v-model="editUserinfo.applicant"
            auto-complete="off"
          ></el-input>
        </el-form-item>

         <el-form-item label="被保险人">
          <el-input
            v-model="editUserinfo.insured"
            auto-complete="off"
          ></el-input>
        </el-form-item>

    <el-form-item label="车主">
          <el-input
            v-model="editUserinfo.vehicel_owner"
            auto-complete="off"
          ></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editDialog = false">取 消</el-button>
        <el-button type="primary" @click="editUser()">提交</el-button>
      </div>
    </el-dialog>

    <!-- 编辑dialog 结束 -->


     <!-- 按照规则匹配所有未匹配渠道的保单 开始  -->
    <el-dialog title="匹配保单" :visible.sync="matchDialog" width="60%">
        
        <el-table :data="match" highlight-current-row style="width: 100%">

              <el-table-column prop="commercial_insure_no" label="商业险投保单号" width="200" sortable>
      </el-table-column>
            
              <el-table-column prop="commercial_insurance_no" label="商业险保单号" width="200" sortable>
      </el-table-column>
              <el-table-column prop="jiaoqiang_insure_no" label="交强险投保单号" width="200" sortable>
      </el-table-column>
            
              <el-table-column prop="jiaoqiang_insurance_no" label="交强险保单号" width="200" sortable>
      </el-table-column>

      

      <el-table-column prop="matched" label="匹配结果" width="200" sortable>
      </el-table-column>
        </el-table>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="matchDialog = false">确定</el-button>
      </div>
    </el-dialog>

    <!-- 按照规则匹配所有未匹配渠道的保单 结束 -->
  </section>
</template>


<script>
import { accMul } from "../../util/calculate.js";
export default {
  data() {
    return {
      filters: {},
      orders: [],
      pageNum: 1,
      pageSize: 20,
      total: 0,
      addUserinfo: {
        group: 2,
      },
      addDialog: false,
      editDialog: false,
      editUserinfo: {},
      // 匹配保单
      matchDialog:false,
      match:[],
    };
  },
  created() {
    this.getOrders();
  },
  methods: {
    handleCurrentChange(val) {
      this.page = val;
      this.getOrders();
    },
    async getOrders() {
      const data = await this.$axios("ChannelMatch/", {
        params: {
          search: this.filters.name,
          page: this.page,
        },
      });

      console.log(data);

      this.orders = data.data;
      this.total = data.count;
    },
    handleEdit(index, row) {
      this.editDialog = true;
      this.editUserinfo = Object.assign({}, row);
    },
    async delOrder(index, row) {
      let res = await this.$axios.delete(`ChannelMatch/${row.id}`);
      console.log(res);
      this.getOrders();
    },
    async addUser() {
      let res = await this.$axios.post("ChannelMatch/", this.addUserinfo);
      //console.log(res)
      this.addDialog = false;
      this.getOrders();
    },
    async editUser() {
      let res = await this.$axios.put(
        `ChannelMatch/${this.editUserinfo.id}/`,
        this.editUserinfo
      );
      console.log(res);
      this.editDialog = false;
      this.getOrders();
    },
    async matchInsurance(){
       
        let res = await this.$axios('ChannelMatch/match_insurance')
        this.match = res.data
        this.$message.success(`匹配成功${res.succ_count}条保单`)
        this.matchDialog = true
    }
  },
};
</script>

<style>
</style>


